import uniStatusBar from "@/components/uni-ui/uni-status-bar/uni-status-bar.vue"
import {mapMutations} from 'vuex';
export default {
	components: {
		uniStatusBar
	}, 
	data() {
		return {
			username:"",
			password:"",
			check:false,
			
			// 验证规则
			rules:{
				
				username:[
					{
						rule:/^1[3456789]\d{9}$/,
						msg:"手机号格式不正确"
					}
				],
				password:[
					{
						rule:/^.{5,20}$/,
						msg:"密码长度必须为5-20个字符"
					}
				]
				
			},
			
			focusClass:{
				username:false,
				password:false
			},
		}
	},
	methods: {
		...mapMutations(['login']),
		goBack(){
			uni.navigateBack({
				delta: 1
			});
		},
		
		toVaild(){
			uni.navigateTo({
				url: './login-vail?phone='+this.username
			})
		},
		// 表单验证
		validate(key){
			var check = true
			this.rules[key].forEach((v)=>{
				if (!(v.rule).test(this[key])) {
					uni.showToast(
						{ 
							title: v.msg, 
							icon: 'none'
						}
					)
					check = false
					return false
				}
			})
			return check
		},
		submit(){
			if (!this.check) {
				return uni.showToast({
					title: '请先同意芒果点餐应用协议',
					icon: 'none'
				});
			}
			if (!this.validate('username')) return;
			// 验证密码
			if (!this.validate('password')) return;
			
			uni.showLoading({
				title: '登录中...',
				mask: true
			})
			
			this.$H.post('/user/user/login',{
				username:this.username,
				password:this.password
			}).then(res=>{
				console.log(res)
				
				this.login(res)
				// 刷新购物车
				uni.$emit('updateCart')
				uni.hideLoading()
				uni.showToast({
					title: '登录成功',
					icon: 'none'
				});
				uni.navigateBack({
					delta:1
				})
			}).catch(err=>{
				console.log(err)
			})
		},
		forget(){
			
		},
		focus(key){
			this.focusClass[key] = true
		},
		blur(key){
			this.focusClass[key] = false
		},
		register(){
			uni.navigateTo({
				url:'./register'
			})
		}
	}
}
